$normal-text-color: #111;
$nav-link-color: #333;
$nav-link-hover-color: black;
$link-color: #444;
$last-p-padd: 1.5em;
$nav-padd: 1em;

body {
  background-color: #fefefe;
  padding: 15px;
  margin: auto;
  max-width: 600px;
}
#wrapper {
  color: $normal-text-color;
  font-family: arial;
  font-size: 14px;
}
h1, h2, h3, h4, h5, h6 {
  font-family: helvetica, arial, sans-serif;
}
h1 {
  font-size: 1.5em;
}
h2 {
  font-size: 1.3em;
}
a {
  text-decoration: none;
  color: $link-color;
}

nav {
  padding: $nav-padd 0px;
  margin: 0px;
}
nav > a:first-of-type {
  padding-left: 0;
}
nav > a {
  padding: 1em;
  color: $nav-link-color;
  font-weight: bold;
  font-family: helvetica, arial, sans-serif;
}
nav > a.on-page {
  color: #888;
}
nav > a:hover {
  text-decoration: underline; 
}

#main-img {
  width: 100%;
}
p {
  padding: .5em 0;
  line-height: 1.4em;
}
.line-under {
  padding-bottom: $last-p-padd;
  border-bottom: 1px solid #aaa;
}
.article {
  font-family: -apple-system, Helvetica, arial, sans-serif;
}

.post-date {
  font-family: -apple-system, Helvetica, arial, sans-serif;
  text-transform: uppercase;
  font-weight: bold;
  color: rgba(0, 0, 0, 0.5);
}

p.post-excerpt {
  margin-top: 0;
  padding-top: 10px;
}

label {
  font-style: italic;
  font-size: .8em;
}

@media screen and (max-width: 600px){
  body {
    width: 90%;
  }
  #info {
    margin: 0 7px;
  }
}

